			.logo {
				span {}
			}

			p {}

			.sns {
				justify-content: space-evenly;

				a {
					&:hover {}
				}
			}
		}

		.nav {
			display: none;

			h2 {}

			.list {
				li {}
			}
		}

		.contactUs {
			.list {
				display: flex;
				flex-wrap: wrap;
			}

			li {
				width: 100%;
				margin-bottom: 22px;

				span {}
			}
		}

		.copy {
			margin-top: 15px;
		}
	}
