* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Roboto', 'Noto Sans KR';

	::-webkit-scrollbar {
		display: none;
	}
}

ol,
ul {
	list-style: none;
}

a {
	text-decoration: none;
	color: inherit;
}

fieldset {
	border: none;
}

legend {
	font-size: 0;
}

caption {
	font-size: 0;
}